@import '../../../../../styles/mixins/mixins';

.team-channel-header__container {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;

  height: var(--header-height);
  min-height: var(--header-height);
  padding: 0 20px;

  color: white;
  //   @include workspace-header-layout;
  //   @include workspace-header-theme;
  background: #17212b;

  .workspace-header__block,
  .workspace-header__block-item {
    overflow-y: hidden;
    display: flex;
    gap: 10px;
    align-items: center;

    min-width: fit-content;
  }

  > .workspace-header__block:first-of-type {
    overflow-x: hidden;

    min-width: 0;
  }

  //   .workspace-header__title,
  //   .workspace-header__subtitle {
  //     white-space: nowrap;
  //   }

  button {
    @include center-content;

    gap: 4px;
  }
}

.team-channel-header__name {
  //   @include ellipsis-text;
  overflow: hidden;

  text-overflow: ellipsis;
  white-space: nowrap;
}

.team-channel-header__name.user {
  //   font: var(--font-weight-medium);
  font-size: 14px;
}

