.channel__container {
  display: flex;
  flex-direction: column;

  width: 100%;

  .str-chat {
    background: #0e1621;
  }
}

.str-chat__ul {
  display: flex;
  flex-direction: column;
  gap: 2px;

  padding: 0 15px 0 10px;
}

.str-chat__li {
  .str-chat__message--other {
    .str-chat__message-simple__actions {
      display: none;
    }
  }
  //   height: 30px;

  //   border: 1px solid black;
}

.str-chat__li--single {
  .str-chat__message--me {
    .str-chat__message-attachment-audio-widget {
      background: #2b5278;
    }

    .str-chat__message-bubble {
      position: relative;

      color: white;

      background: #2b5278;
      border-radius: 15px 15px 0px 15px;

      &::after {
        content: '';

        position: absolute;
        bottom: 0;
        left: 100%;

        display: block;

        width: 10px;
        height: 10px;

        background: radial-gradient(20px at top right, transparent 49%, #2b5278 51%) no-repeat right
          bottom;
      }
    }
  }

  .str-chat__message--other {
    .str-chat__message-attachment-audio-widget {
      background: #182533;
    }

    .str-chat__message-bubble {
      position: relative;

      color: white;

      background: #182533;
      border-radius: 15px 15px 15px 0px;

      &::after {
        content: '';

        position: absolute;
        right: 100%;
        bottom: 0;

        display: block;

        width: 10px;
        height: 10px;

        background: radial-gradient(20px at top left, transparent 49%, #182533 51%) no-repeat right
          bottom;
      }
    }
  }
}

