.str-chat__message-actions-box--open {
  left: -90px;
  transform: translateX(30px);

  background: gray;
  border-radius: 5px;
}
// inside 1
.str-chat__message-actions-list {
  min-width: unset;

  .str-chat__message-actions-list-item-button {
    padding: 7px;

    color: white;

    &:nth-child(1),
    &:nth-child(2) {
      display: none;
    }

    &:hover {
      background: #e5e5e540;
    }
  }
}

.str-chat__message-actions-box-button {
  &:hover {
    background: transparent;
  }
}

.str-chat__message-simple__actions__action--reactions,
.str-chat__message-simple__actions__action--thread {
  display: none;
}

.str-chat__message-input {
  background: #17212b;
}

.str-chat__message-input-inner {
  display: flex;
  align-items: center;
}

.str-chat__file-input-label {
  width: 30px;
  height: 30px;
  margin: 0 0 0 5px;

  background: url('../../../assets/icons/grayClip.svg') no-repeat;

  & * {
    display: none;
  }
}

.str-chat__message-textarea-with-emoji-picker {
  display: flex !important;
  align-items: center !important;

  background: #17212b;

  .str-chat__textarea {
    //  &::before {
    //    cursor: text;
    //    content: 'Type your message';

    //    position: absolute;
    //    z-index: 1;
    //    left: 14px;

    //    color: grey;
    //  }

    & * {
      z-index: 2;

      color: white;

      background: transparent;

      &::placeholder {
        color: gray;
      }
    }
  }

  .str-chat__message-textarea-emoji-picker {
    align-self: unset;
  }
}

.str-chat__date-separator {
  display: flex;
  justify-content: center;

  padding: 4px 0;

  color: white;

  .str-chat__date-separator-line {
    display: none;
  }

  .str-chat__date-separator-date {
    align-self: center;

    padding: 5px 12px;

    background: #1e2c3a;
    border-radius: 30px;
  }
}

